    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3><a style="text-decoration:none;color:darkred"></a><span style="color:gray;"><br />()</span></h3>
      <h6>{{offTimeStart}}</h6>
    </div>

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3><a style="text-decoration:none;color:darkred"></a><span style="color:gray;"><br />()</span></h3>
      <h6>{{offTimeEnd}}</h6>
    </div>
    <br>

    <div class="panel panel-default" id="registerBody">
      <div class="panel-body" >
        <div>
          <center>
            <h3></h3>
            <p></p>
            <p style="color:red;">3</p>
            <br>
              <button v-on:click="recordTime" class="btn btn-primary btn-block " id="register"></button>
            <br>
            <!-- <button @click="$router.push('user')" class="btn btn-lg"></button> -->
            <br></center>
            <h1></h1>
            <p>i) N ()  </br>
ii </br>
iii   <b><span style="font-size:18px"></span></b> <b><span style="font-size:18px"></span></b></br>
