        </div>
      </div>
    </div>
    
  </div>
</div>
</template>
<script>
import axios from 'axios';
import oldnav from './oldnav.vue'


export default
{
  name:'lottery',
  data(){
    return{
      pressed: 0,
      offTimeStart: 0,
      uniDetails: {},
      drawn: 1,
      counter:0,
      token_mystery: '',
      offTimeEnd: 0,
      counter2:0,
      region: '',
    }
  },
  components:{
    oldnav
  },
  computed: {
    isLogged : function(){ return this.$store.getters.isLoggedIn},
    user: function(){
            return JSON.parse(localStorage.getItem('user'));
        }
  },
  created(){
    this.startTime();
    this.showUser();
  },
  methods:{
    recordTime:function(){
        try{
          const token_mystery = localStorage.getItem('token_mystery');
          const drawn = JSON.parse(localStorage.getItem('user')).uni_details.drawn;
          const data = {'token_mystery': token_mystery} //pressed here is to notify backend that user pressed the button
            if(drawn == 1){
              alert("")
              return true;
            }
            if(this.counter2>=3){
              //var myWindow=window.open("","MsgWindow","width=200,height=100");
              alert("");
              //myWindow.document.write("<br/>");
              }
            else{
              this.counter2++;
              axios
                  .put('/Vue/dist/time.php', data, { //api/time/official/store'
                    headers:{
                        Authorization: "Bearer " + localStorage.getItem('token')
                    }
                  })
                  .then(resp=>{
                      if(resp.data.status == 200){
                          const user = JSON.parse(localStorage['user']);
                          const uni = user.uni_details;
                          uni.drawn = 1;
                          localStorage['user'] = JSON.stringify(user);
                          ++this.counter;
                          alert(resp.data.message);
                          this.$router.push('result');
                      }else{
                          alert(resp.data.message);                        
                      }
                  })
          
        }}catch(e){console.log(e)}
    },
    startTime: function(){
      const token_mystery = localStorage.getItem('token_mystery');
      const region = JSON.parse(localStorage.getItem('user')).region;
      axios
        .get('/Vue/dist/start.php',{ //api/time/official/start
          params:{
                    'token_mystery': token_mystery,
                    'region': region
                }
        },{
          headers:{
              Authorization: "Bearer " + localStorage.getItem('token')
          }
        })
        .then(resp=>{
          //console.log(resp.data)
          if(resp.data.status == 200){
              this.offTimeStart = resp.data.offTimeStart; //formatted time
              this.offTimeEnd = resp.data.offTimeEnd;
          }
        })
    },
    showUser: function(){
              
      axios
          .get('api/user',  //'/Vue/dist/user.php'
          {headers: { Authorization: "Bearer " + localStorage.getItem('token')}})
          .then(resp=>{
          //reset the drawn everytime get into this page
          const users = resp.data.user;
          users['uni_details'] = resp.data.uniDetails;
          localStorage['user'] = JSON.stringify(users);
          switch(this.user.region){
            case "Malaysia": this.region = ""; break;
            case "Singapore": this.region = ""; break;
            case "China": this.region = ""; break;
            case "Hong Kong": this.region = ""; break;
            case "Macau": this.region = ""; break;
            case "Taiwan": this.region = ""; break;
            case "Australia": this.region = ""; break;
            default: this.region = this.user.region; break;
          }

        })
    },
    getDrawn: function(){
      axios
        .get('api/time/official/getDraw',{
          headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
          }
        })
        .then(resp=>{
            this.drawn = resp.data.drawn
        })       
    },
  },
}
</script>
<style scoped>
  #register{
    background-color: darkred;
  }
  #register:hover{
    background-color: rgb(199, 0, 0);
  }
  #register.active {
    background-color: rgb(199,0,0);
    box-shadow: 0 5px rgb(199,0,0);
    transform: translateY(5px);
  }
  #registerBody{
    margin: 0 auto;
    position: relative;
  }
</style>



